import React from "react";
import "./Nav.css";

//This function returns the nav brand
function NavBrand(props) {
  return (
    <div className="navbar-brand custom-brand d-flex flex-row align-items-center">
      <span className="text-primary"> </span>
      <span className="text-warning"> </span>
    </div>
  );
}
//This function contains the nav wrapper
function NavWrapper(props) {
  return (
    <nav className="navbar navbar-expand-md fixed-top nav-wrapper border">
      {props.children}
    </nav>
  );
}

//This function returns the login register button pair
function LogRegisterButtonPair(props) {
  return (
    <div className="ml-auto d-flex flex-row align-items-center">
      <a className="btn btn-outline-info" href="/login">
        Log In
      </a>
      <a className="ml-2 btn btn-warning text-white" href="/register">
        Register
      </a>
    </div>
  );
}

function LoggedOutNav(props) {
  return (
    <NavWrapper>
      <NavBrand />
      <LogRegisterButtonPair />
    </NavWrapper>
  );
}

function LoggedInNav(props) {
  return (
    <NavWrapper>
      <NavBrand />
    </NavWrapper>
  );
}
function Nav(props) {
  let { loggedIn } = props;

  let ret = <LoggedOutNav> </LoggedOutNav>;

  if (loggedIn === true) {
    ret = <LoggedInNav> </LoggedInNav>;
  }

  return ret;
}

export { Nav };
